import { StatusBar } from "expo-status-bar";
import React, { useState } from "react";
import {
  Text,
  View,
  TouchableOpacity,
  Image,
  Dimensions,
  TextInput,
  ScrollView,
  Modal,
  ActivityIndicator,
} from "react-native";
import * as ImagePicker from "expo-image-picker";
import BouncyCheckboxGroup, {
  ICheckboxButton,
} from "react-native-bouncy-checkbox-group";
import { KeyboardAwareScrollView } from "react-native-keyboard-aware-scroll-view";
import BottomNav from "../components/BottomNav";
import { Feather } from "@expo/vector-icons";
import { AntDesign } from "@expo/vector-icons";
import { addProduct } from "../services/firestore_product";
import styles from "../styles/AddStyles";

const { width: SCREEN_WIDTH } = Dimensions.get("window");

export default function Add({ navigation }) {
  const [name, setName] = useState("");
  const [price, setPrice] = useState("");
  const [condition, setCondition] = useState("");
  const [size, setSize] = useState("");
  const [detail, setDetail] = useState("");
  const [image, setImage] = useState(null);
  //   
  const [selectedStyles, setSelectedStyles] = useState([]);
  //   
  const [selectedCategories, setSelectedCategories] = useState([]);
  //    
  const [styleModalVisible, setStyleModalVisible] = useState(false);
  //    
  const [categoryModalVisible, setCategoryModalVisible] = useState(false);

  const [isLoading, setIsLoading] = useState(false); //    

  //  
  const stylesList = [
    "Casual",
    "Formal",
    "Sports",
    "Vintage",
    "Romantic",
    "Unique",
    "Travel",
    "Bohemian",
    "Streetwear",
    "Preppy",
    "Chic",
    "Minimalist",
    "Tomboy",
    "Rocker",
    "Safari",
  ];
  //  
  const categoryList = [
    "",
    "",
    "/",
    "",
    "",
    "",
    "",
    "/",
    "",
    "",
  ];

  const checkboxStyles = {
    fillColor: "#91B391",
    unfillColor: "white",
    textStyle: {
      textDecorationLine: "none",
    },
  };

  const conditionCheckboxGroup = [
    {
      id: 0,
      text: "",
      ...checkboxStyles,
    },
    {
      id: 1,
      text: "",
      ...checkboxStyles,
    },
    {
      id: 2,
      text: "",
      ...checkboxStyles,
    },
    {
      id: 3,
      text: "",
      ...checkboxStyles,
    },
  ];

  const sizeCheckboxGroup = [
    {
      id: 0,
      text: "L",
      ...checkboxStyles,
    },
    {
      id: 1,
      text: "M",
      ...checkboxStyles,
    },
    {
      id: 2,
      text: "S",
      ...checkboxStyles,
    },
  ];

  //   
  const pickImage = async () => {
    let result = await ImagePicker.launchImageLibraryAsync({
      mediaTypes: ImagePicker.MediaTypeOptions.All,
      allowsEditing: true,
      aspect: [4, 3],
      quality: 0.2,
    });

    if (!result.canceled) {
      setImage(result.assets[0].uri);
    }
  };

  const deleteImage = async () => {
    if (image) {
      setImage(null);
    }
  };

  const complete = async () => {
    if (!isLoading) {
      setIsLoading(true);
      await addProduct(
        name,
        price,
        condition,
        size,
        selectedCategories,
        selectedStyles,
        detail,
        image,
        navigation
      );
      setTimeout(() => setIsLoading(false), 1000);
    }
  };

  //      
  const handleStyleTagPress = (style) => {
    if (selectedStyles.includes(style)) {
      //    selectedStyles  
      setSelectedStyles(
        selectedStyles.filter((selectedStyle) => selectedStyle !== style)
      );
    } else {
      //    selectedStyles  
      setSelectedStyles([...selectedStyles, style]);
    }
  };
  //      
  const handleCategoryTagPress = (category) => {
    if (selectedCategories.includes(category)) {
      //    selectedCategories  
      setSelectedCategories(
        selectedCategories.filter(
          (selectedCategory) => selectedCategory !== category
        )
      );
    } else {
      //    selectedCategories  
      setSelectedCategories([...selectedCategories, category]);
    }
  };

  //    
  const renderSelectedStyles = () => {
    return (
      <ScrollView horizontal style={styles.selectedItemsContainer}>
        {selectedStyles.map((item, index) => (
          <View key={index} style={styles.selectedItem}>
            <Text style={styles.selectedItemText}>{item}</Text>
            <TouchableOpacity
              onPress={() => {
                handleStyleTagPress(item);
              }}
            >
              <Feather name="x" size={16} color="white" style={styles.icon} />
            </TouchableOpacity>
          </View>
        ))}
      </ScrollView>
    );
  };
  //    
  const renderSelectedCategories = () => {
    return (
      <ScrollView horizontal style={styles.selectedItemsContainer}>
        {selectedCategories.map((item, index) => (
          <View key={index} style={styles.selectedItem}>
            <Text style={styles.selectedItemText}>{item}</Text>
            <TouchableOpacity
              onPress={() => {
                handleCategoryTagPress(item);
              }}
            >
              <Feather name="x" size={16} color="white" style={styles.icon} />
            </TouchableOpacity>
          </View>
        ))}
      </ScrollView>
    );
  };

  //      
  const renderStyleTagButtons = () => {
    return (
      <View style={styles.modalItemContainer}>
        {stylesList.map((style) => (
          <TouchableOpacity
            onPress={() => handleStyleTagPress(style)}
            key={style}
            style={[
              styles.modalItem,
              selectedStyles.includes(style) ? styles.selectedModalItem : null, //    
            ]}
          >
            <Text>{style}</Text>
          </TouchableOpacity>
        ))}
      </View>
    );
  };

  //      
  const renderCategoryTagButtons = () => {
    return (
      <View style={styles.modalItemContainer}>
        {categoryList.map((category) => (
          <TouchableOpacity
            onPress={() => handleCategoryTagPress(category)}
            key={category}
            style={[
              styles.modalItem,
              selectedCategories.includes(category)
                ? styles.selectedModalItem
                : null, //    
            ]}
          >
            <Text>{category}</Text>
          </TouchableOpacity>
        ))}
      </View>
    );
  };

  return (
    <View style={styles.container}>
      <StatusBar style="auto" />

      <View style={styles.menuBar}>
        <TouchableOpacity
          onPress={() => navigation.goBack()}
          style={styles.menuIcon}
        >
          <AntDesign name="close" size={28} color="black" />
        </TouchableOpacity>
        <TouchableOpacity style={styles.menuIcon}>
          {/*      */}
          <Text onPress={complete} style={styles.menuText}>
            
          </Text>
        </TouchableOpacity>
      </View>

      {isLoading ? (
        <ActivityIndicator color="black" size="large" />
      ) : (
        <KeyboardAwareScrollView>
          <ScrollView style={styles.body}>
            {image == null ? (
              <View style={styles.pic}>
                <TouchableOpacity onPress={pickImage}>
                  <Feather name="camera" size={18} color="black" />
                </TouchableOpacity>
              </View>
            ) : (
              <View style={styles.pic}>
                {image && <Image source={{ uri: image }} style={styles.pic} />}
                <TouchableOpacity
                  onPress={deleteImage}
                  style={styles.deletePic}
                >
                  <AntDesign name="close" size={18} color="white" />
                </TouchableOpacity>
              </View>
            )}

            <View style={styles.hr} />

            <View style={styles.inputBox}>
              <View style={styles.labelBox}>
                <Text style={styles.label}></Text>
              </View>
              <TextInput
                onChangeText={setName}
                value={name}
                returnKeyType="done"
                style={styles.input}
              />
            </View>

            <View style={styles.inputBox}>
              <View style={styles.labelBox}>
                <Text style={styles.label}></Text>
              </View>
              <TextInput
                onChangeText={setPrice}
                value={price}
                keyboardType="number-pad"
                returnKeyType="done"
                style={styles.input}
              />
            </View>

            <View style={styles.inputBox}>
              <View style={styles.labelBox}>
                <Text style={styles.label}></Text>
              </View>
              <BouncyCheckboxGroup
                onChange={(ICheckboxButton) => {
                  setCondition(ICheckboxButton.text);
                }}
                data={conditionCheckboxGroup}
                style={styles.checkbox}
              />
            </View>

            <View style={styles.inputBox}>
              <View style={styles.labelBox}>
                <Text style={styles.label}></Text>
              </View>
              <BouncyCheckboxGroup
                onChange={(ICheckboxButton) => {
                  setSize(ICheckboxButton.text);
                }}
                data={sizeCheckboxGroup}
                style={styles.checkbox}
              />
            </View>

            <View style={styles.inputBox}>
              <View style={styles.labelBox}>
                <Text style={styles.label}></Text>
              </View>

              <TouchableOpacity
                onPress={() => setCategoryModalVisible(true)}
                style={styles.addTagBtn}
              >
                <AntDesign name="plus" size={16} color="white" />
              </TouchableOpacity>
              {/*      */}
              <View>{renderSelectedCategories()}</View>
              {/*     */}
              <Modal
                animationType="slide"
                transparent={true}
                visible={categoryModalVisible}
                onRequestClose={() => setCategoryModalVisible(false)}
              >
                <View style={styles.modalContainer}>
                  <View style={styles.modalInner}>
                    <View style={styles.modalHeader}>
                      <Text> </Text>
                      <TouchableOpacity
                        onPress={() => {
                          if (selectedCategories.length <= 2)
                            setCategoryModalVisible(false);
                        }}
                      >
                        <Text style={styles.closeModalButtonText}></Text>
                      </TouchableOpacity>
                    </View>
                    {renderCategoryTagButtons()}
                    {selectedCategories.length > 2 ? (
                      <Text style={styles.maxSelectionText}>
                         2     !
                      </Text>
                    ) : (
                      <Text style={styles.selectionText}>
                           !
                      </Text>
                    )}
                  </View>
                </View>
              </Modal>
            </View>

            <View style={styles.inputBox}>
              <View style={styles.labelBox}>
                <Text style={styles.label}></Text>
              </View>

              <TouchableOpacity
                onPress={() => setStyleModalVisible(true)}
                style={styles.addTagBtn}
              >
                <AntDesign name="plus" size={16} color="white" />
              </TouchableOpacity>
              {/*      */}
              <View>{renderSelectedStyles()}</View>
              {/*     */}
              <Modal
                animationType="slide"
                transparent={true}
                visible={styleModalVisible}
                onRequestClose={() => setStyleModalVisible(false)}
              >
                <View style={styles.modalContainer}>
                  <View style={styles.modalInner}>
                    <View style={styles.modalHeader}>
                      <Text> </Text>
                      <TouchableOpacity
                        onPress={() => {
                          if (selectedStyles.length <= 3)
                            setStyleModalVisible(false);
                        }}
                      >
                        <Text style={styles.closeModalButtonText}></Text>
                      </TouchableOpacity>
                    </View>
                    {renderStyleTagButtons()}
                    {selectedStyles.length > 3 ? (
                      <Text style={styles.maxSelectionText}>
                         3     !
                      </Text>
                    ) : (
                      <Text style={styles.selectionText}>
                           !
                      </Text>
                    )}
                  </View>
                </View>
              </Modal>
            </View>

            {/* input    !*/}
            <View style={styles.inputAreaBox}>
              <View style={styles.labelBox}>
                <Text style={styles.label}> </Text>
              </View>
              <TextInput
                onChangeText={setDetail}
                value={detail}
                multiline={true}
                placeholder="   :D"
                returnKeyType="done"
                blurOnSubmit={true}
                style={styles.inputArea}
              />
            </View>
          </ScrollView>
        </KeyboardAwareScrollView>
      )}

      <BottomNav navigation={navigation} />
    </View>
  );
}
