import { FIRESTORE_DB } from "../firebaseConfig";
import { addDoc, collection, getDocs, query, where } from "firebase/firestore";
import { uploadImage } from "./storage";
import { getUserUid } from "../services/firestore_user";

// firestore    
export async function addProduct(
  name,
  price,
  condition,
  size,
  selectedCategories,
  selectedStyles,
  detail,
  image,
  navigation
) {
  if (name == "") {
    alert(" !");
  } else if (price == 0) {
    alert(" !");
  } else if (condition == "") {
    alert(" !");
  } else if (size == "") {
    alert(" !");
  } else {
    try {
      const uid = await getUserUid();
      const numericPrice = parseInt(price);

      const date = new Date();
      const downloadURL = await uploadImage(uid, image, name, date);

      await addDoc(collection(FIRESTORE_DB, "Product"), {
        uid: uid,
        name: name,
        price: numericPrice,
        condition: condition,
        size: size,
        selectedCategories: selectedCategories,
        selectedStyles: selectedStyles,
        detail: detail,
        date: date, //   
        image: downloadURL,
      });

      alert(`${name}\n  !`);
      navigation.navigate("Store");
    } catch (err) {
      alert("  .\n    :(");
      navigation.navigate("Loading");
      console.log(err);
    }
  }
}

// firestore    
export async function getProduct(src) {
  const productRef = collection(FIRESTORE_DB, "Product");
  const q = query(productRef, where("image", "==", src));
  try {
    const querySnapshot = await getDocs(q);
    const productPromises = querySnapshot.docs.map((doc) => {
      const product = doc.data();
      const productInfo = {
        uid: product.uid, //  uid
        name: product.name,
        price: product.price,
        condition: product.condition,
        size: product.size,
        category: product.selectedCategories,
        style: product.selectedStyles,
        detail: product.detail,
      };
      return productInfo;
    });
    const products = await Promise.all(productPromises);
    return products[0];
  } catch (err) {
    console.log(err);
  }
}
